.inboxCard {
  width: 82%;
}

.inboxCardContainer {
  height: 160px;
  background: #fafcfb;
  border-radius: 32px;
  margin-right: 50px;
  margin-left: 50px;
  border-left-color: #5e9cce; /* Цвет линии слева */
  border-left-style: solid; /* Стиль линии */
  border-left-width: 30px; /* Толщина линии */
  padding-left: 10px;
  margin-top: 50px;
}

.inboxFirstRow {
  display: flex;
  justify-content: space-between;
  padding-right: 30px;
}
