.orderCard {
  background: #fafcfb;
  box-shadow: 0px 0px 10px rgba(129, 129, 129, 0.15);
  border-radius: 16px;
  width: 83%;
  height: 50vh;
  margin-top: 5vh;
  margin-right: 50px;
  margin-left: 40px;
}
.orderCardHeader {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
/* .orderCardBody {
  width: 45%;
  padding: 20px;
} */
.leftOrderCard {
  height: 20vh;
  border-radius: 16px;
  filter: drop-shadow(0px 0px 10px rgba(129, 129, 129, 0.15));
}
.firstRowOrderLCard {
  background: #ecf9fc;
  border: 1px solid #e2e2e2;
  border-radius: 16px 16px 0px 0px;
}
.secondRowOrderLCard {
  border: 0.5px solid #e2e2e2;
}
.thirdRowOrderLCard {
  background: #ecf9fc;
  border: 1px solid #e2e2e2;
  border-radius: 0px 0px 16px 16px;
}

.rightOrderCard {
  border-radius: 16px;
  filter: drop-shadow(0px 0px 10px rgba(129, 129, 129, 0.15));
}
.orderCardBoth {
  width: 100%;
  display: flex;
}
